<template><undefined>
<h1>Tree 树形控件</h1>
<h2 id="he-shi-shi-yong"><a class="header-anchor" href="#he-shi-shi-yong" aria-hidden="true">¶</a> 何时使用</h2>
<p>文件夹、组织架构、生物分类、国家地区等等，世间万物的大多数结构都是树形结构。使用<code v-pre="">树控件</code>可以完整展现其中的层级关系，并具有展开收起选择等交互功能。</p>
<h2 id="dai-ma-yan-shi"><a class="header-anchor" href="#dai-ma-yan-shi" aria-hidden="true">¶</a> 代码演示</h2>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;v-tree :data=\&quot;treeData\&quot; checkable multiple @select=\&quot;selectFn\&quot; @check=\&quot;checkFn\&quot;&gt;&lt;/v-tree&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data() {\n    return {\n      treeData: [{\n        title: 'parent 1',\n        expanded: true,\n        selected: true,\n        children: [{\n          title: 'parent 1-0',\n          expanded: true,\n          disabled: true,\n          children: [{\n            title: 'leaf',\n            disableCheckbox: true\n          }, {\n            title: 'leaf',\n          }]\n        }, {\n          title: 'parent 1-1',\n          checked: true,\n          children: [{\n            title: \&quot;&lt;span style='color: #08c'&gt;sss&lt;/span&gt;\&quot;\n          }]\n        }]\n      }]\n    }\n  },\n  methods: {\n    selectFn(data) {\n      console.log(data);\n    },\n    checkFn(data) {\n      console.log(data);\n    },\n  }\n}\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
  <v-tree :data="treeData" checkable="" multiple @select="selectFn" @check="checkFn"></v-tree>
</template>


</div>
                  <div slot="description"><h4 id="ji-ben"><a class="header-anchor" href="#ji-ben" aria-hidden="true">¶</a> 基本</h4>
<p>最简单的用法，展示可勾选，可选中，禁用，默认展开等功能。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">v-tree</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"treeData"</span> <span class="hljs-attr">checkable</span> <span class="hljs-attr">multiple</span> <span class="hljs-attr">@select</span>=<span class="hljs-string">"selectFn"</span> <span class="hljs-attr">@check</span>=<span class="hljs-string">"checkFn"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-tree</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">treeData</span>: [{
        <span class="hljs-attr">title</span>: <span class="hljs-string">'parent 1'</span>,
        <span class="hljs-attr">expanded</span>: <span class="hljs-literal">true</span>,
        <span class="hljs-attr">selected</span>: <span class="hljs-literal">true</span>,
        <span class="hljs-attr">children</span>: [{
          <span class="hljs-attr">title</span>: <span class="hljs-string">'parent 1-0'</span>,
          <span class="hljs-attr">expanded</span>: <span class="hljs-literal">true</span>,
          <span class="hljs-attr">disabled</span>: <span class="hljs-literal">true</span>,
          <span class="hljs-attr">children</span>: [{
            <span class="hljs-attr">title</span>: <span class="hljs-string">'leaf'</span>,
            <span class="hljs-attr">disableCheckbox</span>: <span class="hljs-literal">true</span>
          }, {
            <span class="hljs-attr">title</span>: <span class="hljs-string">'leaf'</span>,
          }]
        }, {
          <span class="hljs-attr">title</span>: <span class="hljs-string">'parent 1-1'</span>,
          <span class="hljs-attr">checked</span>: <span class="hljs-literal">true</span>,
          <span class="hljs-attr">children</span>: [{
            <span class="hljs-attr">title</span>: <span class="hljs-string">"&lt;span style='color: #08c'&gt;sss&lt;/span&gt;"</span>
          }]
        }]
      }]
    }
  },
  <span class="hljs-attr">methods</span>: {
    selectFn(data) {
      <span class="hljs-built_in">console</span>.log(data);
    },
    checkFn(data) {
      <span class="hljs-built_in">console</span>.log(data);
    },
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div>
