                  <div slot="description"><h4 id="yi-bu-shu-ju-jia-zai"><a class="header-anchor" href="#yi-bu-shu-ju-jia-zai" aria-hidden="true">¶</a> 异步数据加载</h4>
<p>点击展开节点，动态加载数据。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">v-tree</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"asyncData"</span> <span class="hljs-attr">:async</span>=<span class="hljs-string">"getData"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-tree</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">v-tree</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">asyncData</span>: [
        { <span class="hljs-attr">title</span>: <span class="hljs-string">'pNode 01'</span>, <span class="hljs-attr">children</span>: []},
        { <span class="hljs-attr">title</span>: <span class="hljs-string">'pNode 02'</span>, <span class="hljs-attr">children</span>: []},
        { <span class="hljs-attr">title</span>: <span class="hljs-string">'pNode 03'</span>},
      ]
    }
  },
  <span class="hljs-attr">methods</span>: {
    getData(node) {
      <span class="hljs-built_in">console</span>.log(node);
      <span class="hljs-keyword">return</span> <span class="hljs-keyword">new</span> <span class="hljs-built_in">Promise</span>(<span class="hljs-function"><span class="hljs-params">resolve</span> =&gt;</span> {
        setTimeout(<span class="hljs-function"><span class="hljs-params">()</span>=&gt;</span>{
          resolve([
            { <span class="hljs-attr">title</span>: <span class="hljs-string">`leaf <span class="hljs-subst">${node.clue}</span>-0`</span>, <span class="hljs-attr">children</span>: []},
            { <span class="hljs-attr">title</span>: <span class="hljs-string">`leaf <span class="hljs-subst">${node.clue}</span>-1`</span>},
            { <span class="hljs-attr">title</span>: <span class="hljs-string">`leaf <span class="hljs-subst">${node.clue}</span>-2`</span>},
          ])
        }, <span class="hljs-number">1000</span>)
      })
    },
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div>
