<h2 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h2>
<h3 id="tree-props"><a class="header-anchor" href="#tree-props" aria-hidden="true">¶</a> Tree Props</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>data</td>
<td>可嵌套的节点属性的数组，生成tree的数据</td>
<td>Array</td>
<td>[]</td>
</tr>
<tr>
<td>multiple</td>
<td>支持点选多个节点</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>checkable</td>
<td>节点前添加 Checkbox 复选框</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>showLine</td>
<td>是否展示连接线</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>draggable</td>
<td>设置节点可拖拽</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>canDrop</td>
<td>用来判断拖拽时是否能drop的函数,dropPosition的值有-1(插入到目标节点前面),0(插入到目标节点里面),1(插入到目标节点后面)</td>
<td>Function(sourceNode, targetNode, dropPosition)</td>
<td>() =&gt; true</td>
</tr>
<tr>
<td>async</td>
<td>异步加载数据</td>
<td>Function(node)</td>
<td>-</td>
